.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header__flex-gen-container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.header__logo-img {
  margin-top: 5px;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-list li {
  justify-content: center;
  display: flex;
}

.header__nav-item:not(:last-child) {
  margin-right: 20px;
}

.header__nav-link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.header__nav-link:hover, .header__nav-link--is-active {
  color: #00acdc;
}

.header__nav-link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

main {
  grid-template-columns: repeat(1, 200px);
  grid-auto-flow: row;
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
